form{
    width: 400px;
    height: 185px;
    display: block;
    margin: 0px auto 25px;
    background: linear-gradient(#823059, #9e1a32);
    border-radius: 5px;
        box-shadow: 0px 0px 10px 3px #460d35;
    border-bottom-right-radius: 50px;
    padding: 20px 10px
}

input{display: block;outline: none;}

input::placeholder{
    color: #fff;
}

input[type="text"]{
    width: 90%;
    margin: auto;
    height: 40px;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid #fff;
    color: #fff;
    margin-bottom: 17.5px;
    padding-left: 5px;
}

input[type="submit"]{
    width: 30%;
    height: 35px;
    margin-left: 20px;
    border: none;
    background-color: #bd1e54;
    color: #fff;
    margin-bottom: 10px;
    font-size: 16px;
    border-radius: 5px;
    margin-top: 15px;
    cursor: pointer;
}